import React from 'react'
import { formatNumber } from '../../../../helpers/calculo'

export const BoxDate = ({theme="dark",count=0,percent=0,txtdetail=''}) => {

    
    var today = new Date();
    const monthNames = ["Ene", "Feb", "Marc", "Abr", "Mai", "Jun", "Jul", "Aug", "Set", "Oct", "Nov", "Dec"
];
    
    var start_date = '16-' + monthNames[5] + '-2022'; 
   
   
    return (
        <div className={`boxUI flexColumn ${theme}`}>
            <span className="boxDate">{start_date}</span>
            <div className="main">
                <div className="boxBody date">
                    <span className="boxPercent">{percent>0 ? '+' : percent===0 ? '' : '-'} {percent}%</span>
                    <span className="boxCount">{formatNumber(count)}</span>
                    <span className="boxDay">{txtdetail}</span>
                </div>
                <div className="boxFooter mtBox">Personas en Total</div>
            </div>
        </div>
    )
}
