import React from 'react'
import { formatNumber } from '../../../../helpers/calculo'

export const BoxDateRange = ({theme="dark",count=0,txtdetail=''}) => {
    var today = new Date();
    const monthNames = ["Ene", "Feb", "Marc", "Abr", "Mai", "Jun", "Jul", "Aug", "Set", "Oct", "Nov", "Dec"
];
    
    var start_date = '16-' + monthNames[5] + '-2022'; 
    var end_date = today.getDate()+'-'+monthNames[(today.getMonth()+1)]+'-'+ today.getFullYear();
 
    return (
        <div className={`boxUI flexColumn ${theme}`}>

            <div className="rangeDate">
                <span className="boxDate">{start_date}</span>
                <span className="boxDate">{end_date}</span>
            </div>
            <div className="main">
                <div className="boxBody">
                    <span className="boxCount ">{formatNumber(count)}</span>
                    <span className="BoxText mtBox">{txtdetail}</span>
                </div>
                <div className="boxFooter mtBox">Personas en Total</div>
            </div>

        </div>
    )
}
