import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types"

/**
 * @module Actions
 */

/**
 * funcion para traer los datos de los totems
 * @returns callback con los totems
 */
const startToten=()=>{
    return async (dispatch,getState)=>{
        const {groups}=getState().auth;
        
        console.log("GROUPS",groups[0].id);
        console.log("TYPES GROUPDID",types.groupId)
       // const groupId=groups.filter(g=>g.group_id===types.groupId)['id']
        const groupId=7576;
        console.log("GROUPID", groupId)
        const resp = await fetchConToken(`/realplaza/get-person-totals?group_id=${groupId}`);
        const body = await resp.json();
        const {total:totemTotal,resources}=body;
        const tabs=resources.map(r=>{return {name:r.name,code:r.code}})
        const resourcesFinal=resources.map(r=>{return {...r.data}})
        dispatch(getToten({
            totemTotal,
            resources:resourcesFinal,
            tabs
        }))
    }
}

/**
 * funcion para inicializar los totems
 * @param {Object} data Los totems
 */
const getToten=(data)=>({
    type:types.getToten,
    payload:data
})

export{
    startToten
}